import axios from "axios";
import React, { useEffect, useState } from "react";
import "./default.css";
import styled from "styled-components";
import arrow from "./arroww.png";
const WrapperSection = styled.section`
  position: relative;
`;
const WrapperContainer = styled.div`
  padding: 80px 15vw;
  color: #fff;
  text-align: center;
  margin: auto;
  background: #38d16a;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  @media screen and (max-width: 768px) {
    padding: 50px 5vw;
  }
`;
const Poligon = styled.div`
  position: absolute;
  bottom: -0px;
  left: 50%;
  transform: translateX(-50%);
  img {
    width: 60px;
    height: 60px;
  }
`;
const WrapperItem = styled.div`
  display: flex;
  margin: auto;
`;
const Box = styled.div`
  border-radius: 5px;
  transition: 300ms ease;
  &:hover {
    background: #ef476f;
    cursor: default;
  }
`;
const WrapperOne = ({ data }) => {
  const [counter, setCounter] = useState([]);
  // const data = [
  //   {
  //     num: "520%",
  //     text: "PORCENTAJE",
  //   },
  //   {
  //     num: "99%",
  //     text: "TURISTAS CONTENTOS",
  //   },
  //   {
  //     num: "25",
  //     text: "LUGARES TURISTICOS",
  //   },
  //   {
  //     num: "10.380",
  //     text: "POBLACION",
  //   },
  // ];
  useEffect(() => {
    getVisitCounter();
    setVisitCounter();
  }, []);
  const getVisitCounter = async () => {
    const result = await axios.get(`${process.env.REACT_APP_API_URL}/system`);
    setCounter(result.data.counter);
  };
  const setVisitCounter = async () => {
    await axios.post(`${process.env.REACT_APP_API_URL}/system`);
  };
  return (
    <WrapperSection>
      <WrapperContainer>
        <WrapperItem>
          <Box>
            <h1>{counter}</h1>
            <p>VISITAS</p>
          </Box>
        </WrapperItem>
        <WrapperItem>
          <Box>
            <h1>{counter}</h1>
            <p>VISITAS</p>
          </Box>
        </WrapperItem>
        <WrapperItem>
          <Box>
            <h1>{counter}</h1>
            <p>VISITAS</p>
          </Box>
        </WrapperItem>
        <WrapperItem>
          <Box>
            <h1>{counter}</h1>
            <p>VISITAS</p>
          </Box>
        </WrapperItem>
      </WrapperContainer>
      <Poligon>
        <img src={arrow} alt="" />
      </Poligon>
      {/* <section className="Branding wrapperOne">
        <div className="container grid1">
          {data.map((value) => {
            return (
              <div className="box">
                <h1>{value.num}</h1>
                <p>{value.text}</p>
              </div>
            );
          })}
          <div className="box">
            <h1>{counter}</h1>
            <p>VISITAS</p>
          </div>
        </div>
      </section> */}
    </WrapperSection>
  );
};

export default WrapperOne;
