import React, { useEffect, useState } from "react";

import { Content } from "../components/Content/Content";

// import Hero from "../components/Hero/Hero";
// import Features from "../components/Features/Features";
// import Carousel from "../components/Carousel/Carousel";
// import { Navbar } from "../components/Navbar";
import { Hero } from "../components/Hero";
import { heroOne, heroTwo, heroThree } from "../data/HeroData";
// import Navbar from "../components/Navbar/Navbar";
// import Footer from "../components/Footer/Footer";
import { SliderData } from "../data/SliderData";

import PersonInfo from "../components/PersonInfo";
import Footer from "../components/Footer";
import { Container, Section } from "../globalStyles";
import { VideoYoutube } from "../components/Video/VideoYoutube";
import Work from "../components/Gallery/Work";
import WrapperOne from "../components/WrapperOne";
import { data } from "../data/WrapperData";
import { HomeGallery } from "../components/Gallery/HomeGallery";
import axios from "axios";

const Home = () => {
  const [visit, setVisit] = useState([]);

  useEffect(() => {
    setCounterVisits();
    getCounterVisits();
  }, []);

  const setCounterVisits = async () => {
    await axios.post(`${process.env.REACT_APP_API_URL}/system`);
  };
  const getCounterVisits = async () => {
    const counter = await axios.get(`${process.env.REACT_APP_API_URL}/system`);
    setVisit(counter.data);
  };
  return (
    <>
      {/* <Navbar /> */}

      <Hero slides={SliderData} />

      {/* <button onClick={subscribeUser}>Click Here</button> */}
      <WrapperOne data={data} />
      <Content {...heroOne} />
      <Content {...heroTwo} />
      <Content {...heroThree} />
      {/* <WrapperOne data={data} /> */}
      {/* <h1>Visitas{visit.counter}</h1> */}
      <WrapperOne data={data} /> 
      <HomeGallery />
      <VideoYoutube />
      <Section>
        <Container>
          <PersonInfo />
        </Container>
      </Section>
      <Footer />
    </>
  );
};

export default Home;
