import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./style.css";
import moment from "moment";
import DateTimePicker from "react-datetime-picker";
import Modal from "react-modal";
import Swal from "sweetalert2";
import "./style.css";
import { uiCloseModal } from "../../actions/ui";
import {
  eventClearActiveEvent,
  eventStartAddNew,
  eventStartUpdate,
} from "../../actions/events";
import { useLoadScript } from "@react-google-maps/api";
import { MapPlace } from "../Map/MapPlace";
import axios from "axios";
import { FaWindowClose } from "react-icons/fa";
import logo from "./logo.png";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
Modal.setAppElement("#root");

const now = moment().minutes(0).seconds(0).add(1, "hours");
const nowone = now.clone().add(1, "hours");

const initEvent = {
  title: "",
  notes: "",
  start: now.toDate(),
  end: nowone.toDate(),
  place: "",
};

export const CalendarModal = () => {
  const { isLoaded } = useLoadScript({
    googleMapsApiKey: "AIzaSyAKMws4QJbXE3xtlmJRBpJwfk1BUCUMEhg", // Add your API key
  });
  const { modalOpen } = useSelector((state) => state.ui);
  const { activeEvent } = useSelector((state) => state.calendar);
  const { uid } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const [sitios, setSitios] = useState([]);
  const [dateStart, setDateStart] = useState(now.toDate());
  const [dateEnd, setDateEnd] = useState(nowone.toDate());
  const [titleValid, setTitleValid] = useState(true);
  const [file, setFile] = useState(null);
  const [formValues, setFormValues] = useState(initEvent);
  const PF = process.env.REACT_APP_IMG_URL + "/images/";
  const { notes, title, start, end, place } = formValues;

  useEffect(() => {
    getEvents();
    if (activeEvent) {
      setFormValues(activeEvent);
    } else {
      setFormValues(initEvent);
    }
  }, [activeEvent, setFormValues]);

  const handleInputChange = ({ target }) => {
    setFormValues({
      ...formValues,
      [target.name]: target.value,
    });
  };
  const getEvents = async () => {
    const res = await axios.get(`${process.env.REACT_APP_API_URL}/sitios`);
    setSitios(res.data);
  };

  const closeModal = () => {
    dispatch(uiCloseModal());
    dispatch(eventClearActiveEvent());
    setFormValues(initEvent);
  };

  const handleStartDateChange = (e) => {
    setDateStart(e);
    setFormValues({
      ...formValues,
      start: e,
    });
  };
  const handleEndDateChange = (e) => {
    setDateEnd(e);
    setFormValues({
      ...formValues,
      end: e,
    });
  };
  const handleSubmitForm = async (e) => {
    e.preventDefault();

    const momentStart = moment(start);
    const momentEnd = moment(end);
    if (momentStart.isSameOrAfter(momentEnd)) {
      return Swal.fire(
        "Error",
        "La fecha fin debe de ser mayor a la fecha de inicio",
        "error"
      );
    }

    if (file) {
      const data = new FormData();
      const filename = Date.now() + file.name;
      data.append("name", filename);
      data.append("file", file);
      formValues.photo = filename;
      try {
        await axios.post(`${process.env.REACT_APP_API_URL}/upload`, data);
      } catch (err) {}
    }

    if (title.trim().length < 2) {
      return setTitleValid(false);
    }

    if (activeEvent) {
      dispatch(eventStartUpdate(formValues));
    } else {
      dispatch(eventStartAddNew(formValues));
    }
    setTitleValid(true);
    enviarNotificacion();
    closeModal();
  };

  const enviarNotificacion = async () => {
    await axios.post(`${process.env.REACT_APP_API_URL}/notification/push`, {
      titulo: "EVENTO PROXIMO: " + formValues.title,
      cuerpo: formValues.notes,
      usuario: "tarqui",
    });
    //console.log("form", formValues);
  };
  return (
    <Modal
      isOpen={modalOpen}
      onRequestClose={closeModal}
      style={customStyles}
      closeTimeoutMS={300}
      className="modal"
      overlayClassName="modal-fondo"
    >
      <div
        className="header-modal"
        style={{ display: "flex", justifyContent: "space-between" }}
      >
        <h1> Detalles de Evento </h1>
        <button
          className="btn"
          onClick={() => {
            dispatch(uiCloseModal());
            setFormValues(initEvent);
          }}
        >
          <FaWindowClose size={40} style={{ color: "#ef476f" }} />
        </button>
      </div>

      <hr />
      <form className="container" onSubmit={handleSubmitForm}>
        <div className="form-group">
          <label>Fecha y hora inicio</label>
          <DateTimePicker
            onChange={handleStartDateChange}
            value={dateStart}
            className="form-control"
            disabled={uid ? false : true}
          />
        </div>

        <div className="form-group">
          <label>Fecha y hora fin</label>
          <DateTimePicker
            onChange={handleEndDateChange}
            value={dateEnd}
            minDate={dateStart}
            className="form-control"
            disabled={uid ? false : true}
          />
        </div>

        <hr />

        <div className="form-group">
          <input
            type="text"
            className={`form-control ${!titleValid && "is-invalid"}`}
            placeholder="Título del evento"
            name="title"
            autoComplete="off"
            value={title}
            onChange={handleInputChange}
            disabled={uid ? false : true}
          />
        </div>

        <div className="form-group">
          <textarea
            type="text"
            className="form-control"
            placeholder="Descripcion"
            rows="5"
            name="notes"
            value={notes}
            onChange={handleInputChange}
            disabled={uid ? false : true}
          ></textarea>
        </div>

        {/* <div className="form-group">
          <select
            name="place"
            class="form-select"
            value={place}
            onChange={handleInputChange}
            disabled={uid ? false : true}
          >
            {coords.map((item) => (
              <option selected key={item.id} value={item.id}>
                {item.name}
              </option>
            ))}
          </select>
        </div> */}

        <div className="form-group">
          <select
            name="place"
            className="form-select"
            value={place}
            onChange={handleInputChange}
            disabled={uid ? false : true}
          >
            <option selected>Sin Ubicación</option>
            {sitios.map((item) => (
              <option key={item._id} value={item._id}>
                {item.title}
              </option>
            ))}
          </select>
        </div>

        {uid === undefined && (
          <div>{isLoaded ? <MapPlace place={place} /> : null}</div>
        )}
        {uid !== undefined ? (
          <div className="form-group">
            <input
              type="file"
              className={`form-control ${!titleValid && "is-invalid"}`}
              name="file"
              onChange={(e) => setFile(e.target.files[0])}
              accept="image/*"
              // value={logo}
            />
            {/* <label for="img" class="btn btn-info">
            Seleccionar Imagen
          </label>
          <input type="file" id="img" style={{ display: "none" }} /> */}
          </div>
        ) : (
          <img
            style={{ "margin-top": "15px" }}
            width="100%"
            src={PF + formValues.photo}
            alt=""
          />
        )}
        {uid !== undefined && (
          <button type="submit" className="btn btn-outline-primary btn-block">
            <i className="far fa-save"></i>
            <span> Guardar</span>
          </button>
        )}
      </form>
    </Modal>
  );
};
